export default function Dashboard() {
  return (
    <div>
      <h1 className="text-2xl font-bold mb-4">Dashboard Overview</h1>
      <div className="grid md:grid-cols-3 gap-6">
        <Card title="Total Sales Today" value="UGX 2,350,000" color="bg-teal-600" />
        <Card title="Shift Hours" value="6 hrs" color="bg-yellow-600" />
        <Card title="Pending Alerts" value="2" color="bg-red-600" />
      </div>
    </div>
  )
}

function Card({ title, value, color }: { title: string; value: string; color: string }) {
  return (
    <div
      className={`p-6 rounded-2xl text-white shadow-lg focus:ring-2 focus:ring-offset-2 ${color}`}
      role="status"
      aria-label={title}
      tabIndex={0}
    >
      <h2 className="text-lg font-semibold">{title}</h2>
      <p className="text-3xl mt-2">{value}</p>
    </div>
  )
}
